import { lazy } from "react";

const About = lazy(() => import("../pages/about"));
const ContactPage = lazy(() => import("../pages/contactPage"));
const LoginPage = lazy(() => import("../pages/loginPage"));
const ProfilePage = lazy(() => import("../pages/proflePage"));
const OtpPage = lazy(() => import("../pages/otpPage"));
const SuccessPage = lazy(() => import("../pages/successPage"));
const Products = lazy(() => import("../pages/Products"));
const Profile = lazy(() => import("../pages/proflePage"));

const routes = [
  {
    name: "About",
    path: "/about",
    component: About,
  },
  {
    name: "Contact",
    path: "/contact",
    component: ContactPage,
  },
  {
    name: "Login",
    path: "/login",
    component: LoginPage,
  },
  {
    name: "Profile",
    path: "/profile",
    component: ProfilePage,
  },
  {
    name: "Otp",
    path: "/otp",
    component: OtpPage,
  },
  {
    name: "Success",
    path: "/success",
    component: SuccessPage,
  },
  {
    name: "Products",
    path: "/products",
    component: Products,
  },
  {
    name: "Profile",
    path: "/my/account",
    component: Profile,
  },
];

export default routes;
