import { useEffect, useState } from "react";
import "./addProduct.css";
import { useDispatch, useSelector } from "react-redux";
import { addProduct, EMPTY_PRODUCT_STATE } from "../../../actions";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import Loader from "../../login/loader";
import { zipSync, strToU8 } from "fflate"; // Import fflate for file compression

const AddProduct = () => {
  //* states
  const [name, setProductName] = useState("");
  const [description, setDescription] = useState("");
  const [file, setFile] = useState(null);
  const [productType, setProductType] = useState("");
  const dispatch = useDispatch();
  const success = useSelector((state) => state.product.success);
  const error = useSelector((state) => state.product.error);
  const isLoading = useSelector((state) => state.product.loading);

  // todo: useEffect for handling success and error message
  useEffect(() => {
    if (success) {
      setProductName("");
      setDescription("");
      setProductType("");
      setFile(null);
    }
    if (error) {
      dispatch({ type: EMPTY_PRODUCT_STATE });
      setFile(null);
    }
  }, [success, error]);

  //! function for file validation (check type)
  const handleFileChange = (e) => {
    const selectedFile = e.target.files[0];

    if (selectedFile) {
      if (selectedFile.name.split(".").pop() !== "zip") {
        alert("Only ZIP files are allowed.");
        setFile(null);
        return;
      }

      setFile(selectedFile);
    }
  };

  //! function for compressing the file
  const compressFile = async (file) => {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsArrayBuffer(file);
      reader.onload = () => {
        try {
          // Compress the file with fflate
          const compressed = zipSync({
            [file.name]: new Uint8Array(reader.result),
          });
          const blob = new Blob([compressed], { type: "application/zip" });
          resolve(blob);
        } catch (error) {
          reject(error);
        }
      };
      reader.onerror = (error) => reject(error);
    });
  };

  //? function for adding product
  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!name.trim() || !description.trim() || !file || !productType) {
      alert("Please fill all the details and upload a ZIP file.");
    } else {
      try {
        const compressedFile = await compressFile(file); // Compress the file

        const formData = new FormData();
        formData.append("name", name);
        formData.append("description", description);
        formData.append("type", productType);
        formData.append("productPdf", compressedFile, file.name); // Append compressed file

        dispatch(addProduct(formData)); // Assuming your Redux action can handle FormData

        setFile(null);
      } catch (error) {
        alert("File compression failed.");
        console.error(error);
      }
    }
  };

  return (
    <>
      {isLoading ? (
        <Loader />
      ) : (
        <div className="container my-5 form-container">
          <div className="row justify-content-center" style={{ gap: 100 }}>
            <div className="col-md-4 form-col">
              <form onSubmit={handleSubmit}>
                <div className="mb-3">
                  <label htmlFor="productName" className="form-label">
                    Product Name
                  </label>
                  <input
                    type="text"
                    className="form-control"
                    id="productName"
                    value={name}
                    onChange={(e) => setProductName(e.target.value)}
                  />
                </div>

                <div className="mb-3">
                  <label htmlFor="productDescription" className="form-label">
                    Product Description
                  </label>
                  <textarea
                    className="form-control"
                    id="productDescription"
                    value={description}
                    onChange={(e) => setDescription(e.target.value)}
                    rows="3"
                  ></textarea>
                </div>

                <div className="mb-3">
                  <label htmlFor="productFile" className="form-label">
                    Product File (ZIP Only)
                  </label>
                  <input
                    type="file"
                    className="form-control"
                    id="productFile"
                    accept=".zip"
                    onChange={handleFileChange}
                  />
                </div>

                <div className="mb-3">
                  <label htmlFor="productName" className="form-label">
                    Product Type
                  </label>
                  <select
                    value={productType}
                    onChange={(e) => setProductType(e.target.value)}
                    className="form-select"
                  >
                    <option value="">Select product type</option>
                    <option value="community">Community</option>
                    <option value="professional">Professional</option>
                  </select>
                </div>

                <button type="submit" className="btn btn-success w-100 mt-3">
                  Add Product
                </button>
              </form>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default AddProduct;
